@font-face {
    font-family: 'Neo Sans';
    src: url('data:font/woff2;charset=utf-8;base64,') format('woff2'),
        url('data:font/woff;charset=utf-8;base64,') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}
*{
    //font-family: "Neo Sans"
}
a, a:hover, a:focus, a:active {
    text-decoration: none;
    color: inherit;
}
h1{
    font-size: clamp(26px, 10vw, 64px); 
    margin-bottom: 3rem;
    text-shadow: 1px 1px 2px $text-color;
}
h2{
    font-size: clamp(24px, 10vw, 46px); 
    margin-bottom: 2rem;
}
h3{
    font-size: clamp(20px, 10vw, 38px); 
    margin-bottom: 2rem;
}
h4{
    font-size: clamp(18px, 10vw, 32px); 
    margin-bottom: 2rem;
}
p{
    font-size: clamp(16px, 10vw, 20px); 

}


